<template>
  <div>
    <div class="content">
            <div id="step"><h1 class="has-text-centered">1-qadam</h1>
                <p class="subtitle mb-3">1. 2 likdan 8 likka o'tkazish</p></div>
            <form @submit.prevent>
                <div class="columns">
                    <div class="column">
                        <div class="field">
                            <label>Sonni kirirting:</label>
            
                            <div class="control">
                                <input v-model="asos" class="input">
                            </div>
                        </div>
                        <div class="field">
                            <div :class="classs">
                                <select name="cars" @change="from=$event.target.value" class="select" id="cars" ref="cars" >
                                    <option :value="from">{{from}}</option>
                                </select>
                            </div>
                            <div class="select">
                                <select name="cars" @change="to=$event.target.value" class="select" id="cars1" ref="cars1">
                                    <option value=2>2</option>
                                    <option value=8>8</option>
                                    <option value=10 selected>10</option>
                                    <option value=16>16</option>
                                </select>
                                
                            </div>
                        </div>
                        <div class="field">
                            <label>Javobni kiriting:</label>
            
                            <div class="control">
                                <input v-model="javob" class="input">
                            </div>
                        </div>
                        <div class="field">
                            <div class="control">
                                <button @click="hisobla" class="button is-primary">Hisoblash</button>
                            </div>
                        </div>
                        <div class="field" v-if="natija"><h3 :class="natijac">{{natija}}</h3></div>
                        <!--  -->
                        <div :class="disp">
                            <div id="step"><h1 class="has-text-centered">2-qadam</h1>
                                <p class="subtitle mb-3">1. 2 likdan 10 likka o'tkazish</p></div>
                                <div class="field">
                                        <label>Sonni kirirting:</label>
                        
                                        <div class="control">
                                            <input v-model="asos1" class="input">
                                        </div>
                                    </div>
                                    <div class="field">
                                        <div class="select is-danger disabled">
                                            <select name="cars" @change="from=$event.target.value" class="select" id="cars2" ref="cars2" >
                                                <option :value="from">{{from}}</option>
                                            </select>
                                        </div>
                                        <div class="select">
                                            <select name="cars" @change="to=$event.target.value" class="select" id="cars3" ref="cars2">
                                                <option value=2>2</option>
                                                <option value=8 selected>8</option>
                                                <option value=10>10</option>
                                                <option value=16>16</option>
                                            </select>
                                            
                                        </div>
                                    </div>
                                    <div class="field">
                                        <label>Javobni kiriting:</label>
                        
                                        <div class="control">
                                            <input v-model="javob1" class="input">
                                        </div>
                                    </div>
                                    <div class="field">
                                        <div class="control">
                                            <button @click="hisobla10" class="button is-primary">Hisoblash</button>
                                        </div>
                                    </div>
                                    <div class="field" v-if="natija1"><h3 :class="natijac">{{natija1}}</h3></div>
                        </div>
                    </div>
                </div>
            </form>
        </div>
  </div>
</template>

<script>
export default {
  name: 'Sanoq8',
  data(){
            return{
                asos:null,
                javob:null,
                asos1:null,
                javob1:null,
                asos2:null,
                javob2:null,
                natija:null,
                natija1:null,
                natija2:null,
                from:2,
                to:10,
                to1:10,
                to2:16,
                classs:'select',
                natijac:'title',
                step1:false,
                disp:"display is-hidden",
                disp1:"display is-hidden",
                asos1:null,
                javob1:null,
            }
        },
        mounted(){
          this.$refs.cars.disabled=true;
          this.classs='select is-danger disabled';
          this.step1=true;
        }
        ,
        methods:{
            hisobla(){
                var fromns=parseInt(this.from);
                var tons=parseInt(this.to);
                var i=parseInt(this.asos,fromns);
                var hisob=i.toString(tons);
                console.log(hisob);
                if((this.javob)===hisob){
                    this.natija=hisob;
                    this.natijac='title is-success'
                    if(this.step1){
                        this.disp='display is-block';
                    }
                }else if(this.javob!==hisob){
                    this.natija='Noto\'g\'ri';
                    this.natijac='title has-text-danger'
                    console.log(hisob,this.javob);
                }else{
                    this.natija='Javob bo\'sh iltimos to\'ldiring!';
                    this.natijac='title has-text-danger'}
                
            },
            hisobla10(){
                var fromns=parseInt(this.from);
                var tons=parseInt(this.to);
                var i=parseInt(this.asos1,fromns);
                var hisob=i.toString(tons);
                console.log(hisob);
                if((this.javob1)===hisob){
                    this.natija1=hisob;
                    this.natijac='title is-success'
                    this.disp1='display is-block';
                }else if(this.javob1!==hisob){
                    this.natija1='Noto\'g\'ri';
                    this.natijac='title has-text-danger'
                    console.log(hisob,this.javob);
                }else{
                    this.natija1='Javob bo\'sh iltimos to\'ldiring!';
                    this.natijac='title has-text-danger'}
            },
            hisobla16(){
                var fromns=parseInt(this.from);
                var tons=parseInt(this.to);
                var i=parseInt(this.asos2,fromns);
                var hisob=i.toString(tons);
                console.log(hisob);
                if((this.javob2)===hisob){
                    this.natij2=hisob;
                    this.natijac='title is-success'
                }else if(this.javob2!==hisob){
                    this.natija2='Noto\'g\'ri';
                    this.natijac='title has-text-danger'
                    console.log(hisob,this.javob);
                }else{
                    this.natija2='Javob bo\'sh iltimos to\'ldiring!';
                    this.natijac='title has-text-danger'}
            }
        }
}
</script>

